* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.flickity-gallery {
  height: 50%;
  background: #eee;
  position: relative;
  overflow-x: clip;
}

.flickity-gallery-cell {
  width: 28%;
  counter-increment: gallery-cell;
  background: none;
  margin-right: 10px;
}

.flickity-gallery-cell.is-selected {
  background: none;
}

.flickity-gallery-cell b {
  vertical-align: middle;
  font-family: Courier New, Courier, monospace;
  font-size: 70px;
  line-height: 200px;
}

.flickity-prev-next-button, .previous, .next, .flickity-page-dots {
  display: none;
}

